<template>
  <div id="dashboard">
    <ul class="collection with-header">
      <li class="collection-header">
        <h4>Employees</h4>
      </li>
      <li v-for="employee in employees" 
        v-bind:key="employee.id" 
        class="collection-item">
        <div class="chip">{{employee.dept}}</div>{{employee.employee_id}}:{{ employee.name }}

        <router-link class="secondary-content" v-bind:to="{name: 'view-employee', params: {employee_id: employee.employee_id}}">
          <i class="fa fa-eye"></i>
        </router-link>
      </li>
    </ul>
    
    <div class="fixed-action-btn">
      <router-link 
      to="/new" 
      class="btn-floating btn-large red">
        <i class="fa fa-plus"></i>
      </router-link>
    </div>
  </div>
</template>


<script>
import db from "./firebaseInit";

export default {
  name: "dashboard",
  data() {
    return {
      // employee is the property that we will fetch the data in.
      employees: []
    };
  },
  created() {
    // This created method will run when the component is created or loaded.

    // how to get data from firebase
    // https://firebase.google.com/docs/firestore/query-data/get-data
    db
      .collection("employees")
      .orderBy("dept")
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          const data = {
            id: doc.id,
            employee_id: doc.data().employee_id,
            name: doc.data().name,
            dept: doc.data().dept,
            position: doc.data().position
          };
          // assign the fetch data into employee property
          this.employees.push(data);
        });
      });
  }
};
</script>


